<template>
  <div class="container" id="app">
  	<div class="row center-xs">
  		<div class="col-xs-1" id="left">
			<section id="section">
				<router-link to="/"><span>index</span></router-link>
				<router-link to="/article"><span>article</span></router-link>
				<router-link to="/link"><span>link</span></router-link>
				<router-link to="/about"><span>about</span></router-link>
			</section>
  		</div>
  		<div class="col-xs-6">
  			<div id="content">
  				<router-view/>
  			</div>
  			<footer>
  				<span>{{ $t("message.sankou") }}<a href="https://otakism.com/" target="_ost">宅談</a></span>，
  				<span><a @click="setLanguage('zh')">中文</a>
  					<a @click="setLanguage('ja')">日本語</a></span>
  				<span style="display: inline-block;">© 2018 ゆきの</span>
  			</footer>
  		</div>
  	</div>
